		countryChange(value){
			this.ruleForm.city = '';
			this.ruleForm.school = '';
			this.cityList = [];
			this.schoolList = [];
			this.countryList.map(e=>{//遍历数据
				if( value == e.zhname){
					this.cityList = e.city;
					return;
				}
			})
		},
		cityChange(value){
			this.ruleForm.school = '';
			this.schoolList = [];
			this.cityList.map(e=>{//遍历数据
				if( value == e.cityname){
					this.schoolList = e.school;
					return;
				}
			}) 
		},
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.fullscreenLoading = true;
					var that = this;
					let parmasData={
						type:'0',
						name:this.ruleForm.name ,
						areacode:this.ruleForm.areacode ,
						phone:this.ruleForm.phone ,
						weixin:this.ruleForm.weixin ,
						country:this.ruleForm.country ,
						city:this.ruleForm.city ,
						school:this.ruleForm.school ,
						remark:this.ruleForm.remark 
					}
					this.$request.savehelptickets(parmasData).then(res=>{
						this.dialogVisible = true
						this.fullscreenLoading = false;
						window._agl && window._agl.push(['track', ['success', {t: 3}]])
					}).catch(e=>{
						this.fullscreenLoading = false;
						this.$message({
							message: '数据提交失败',
							type: 'error',
						});
					})
					
				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
